@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:400,700,900);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* END RESET */

html {
  --color-yellow: #fcb813;
}

body {
  background-image: linear-gradient(
    to bottom,
    #182072,
    #00428f,
    #0062a4,
    #0081b4,
    #309fc1
  );
  height: 100vh;
  font-family: "Noto Sans KR", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
  font-size: 16px;
}

input {
  outline: none;
}

.wrapper {
  margin: 0 auto;
  max-width: 280px;
}

header {
  margin-bottom: 48px;
}

header .flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
}

header ul {
  display: flex;
  flex-direction: row;
}

#logo {
  padding-left: 0;
}

a {
  padding-left: 16px;
  font-size: 10px;
  line-height: 19px;
  font-weight: 700;
  color: inherit;
  text-transform: uppercase;
  text-decoration: none;
  transition: 0.3s;
}

a:hover {
  color: #fcb813;
  color: var(--color-yellow);
}

h1 {
  font-weight: 900;
  letter-spacing: 1px;
  font-size: 1.5em;
  padding-left: 10px;
}

h1::before {
  content: "";
  width: 0;
  height: 100%;
  position: relative;
  border: 2px solid #fcb813;
  border: 2px solid var(--color-yellow);
  top: 0;
  left: -10px;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  margin-bottom: 64px;
  /* height: 500px; */
  background-color: rgba(0, 34, 114, 0.65);
  border-radius: 10px;
  padding: 40px 32px 40px;
}

.form {
  display: flex;
  flex-direction: column;
}

.form-flex {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  position: relative;
}

.form-flex:last-child {
  justify-content: flex-end;
}

.form label {
  padding-bottom: 8px;
  font-size: 0.7em;
}

.form input,
.form select,
#result input {
  box-sizing: border-box;
  color: rgb(0, 34, 114);
  font-size: 1.5em;
  height: 60px;
  padding-left: 20px;
  width: 100%;
  background: rgb(255, 255, 255);
  border-width: 0px;
  border-style: none;
  border-style: initial;
  border-color: currentColor;
  border-color: initial;
  border-image: none;
  border-image: initial;
  border-radius: 10px;
}

.form input[type="submit"] {
  width: auto;
  background-color: #fcb813;
  background-color: var(--color-yellow);
  cursor: pointer;
}

.form input[type="submit"]:focus {
  outline: none;
}

#result {
  margin-top: 30px;
}

#result p {
  font-size: 80px;
}
#result span {
  font-size: 27px;
}

.error-message {
  position: absolute;
  top: -24px;
  color: red;
}

/* Larger smart phone */
@media (min-width: 480px) {
  .wrapper {
    max-width: 450px;
  }
  a {
    font-size: 14px;
  }
  .form input,
  .form select,
  #result input {
    font-size: 2.2em;
  }
  .card {
    justify-content: center;
    padding: 80px 32px 80px;
  }
  #result {
    margin-top: 64px;
  }
  .form-flex {
    margin-top: 16px;
  }
}

/* Tablet */
@media (min-width: 768px) {
  .wrapper {
    max-width: 700px;
  }
  .form {
    flex-direction: row;
  }
  .form-flex {
    flex-direction: column;
    margin: 16px;
  }
}

/* Large desktop */
@media (min-width: 1200px) {
  .wrapper {
    max-width: 1024px;
  }
}


